    import back from '@/components/back'
	import noData from '@/components/noData'
    export default {
        components: {
			back,
			noData
        },
        data () {
            return {
                imgs: [require("../../assets/default/doctor.png"), require("../../assets/default/doctor.png")],
                id: this.$route.query.id,
                type: this.$route.query.type,
                datas: {},
                imgList: []
            }
        },
        created() {
            this.getInfo()
        },
        methods: {
            preView (index) {
                const data = {
					position: index,
					url: this.imgList
				}
				this.$bridge.callhandler('Preview', JSON.stringify(data))
            },
            getInfo () {
                // ConsultAPI/Consult/GetConsultInfo 图文问诊订单详情
                // ConsultAPI/Consult/GetInquiryInfo 视频问诊订单详情
                // ConsultAPI/Consult/GetRemoteOutpatientInfo 远程门诊订单详情 
                // Prescription/Prescription/GetPrescriptionApplyInfo 续方详情
                let url = ''
                if (this.type == 1 || this.type == 5) {
                    url = 'ConsultAPI/Consult/GetConsultInfo'
                }else if (this.type == 2 || this.type == 6) {
                    url = 'ConsultAPI/Consult/GetInquiryInfo'
                }else if (this.type == 15) {
                    url = 'ConsultAPI/Consult/GetRemoteOutpatientInfo'
                }else if (this.type == 3) {
                    url = 'Prescription/Prescription/GetPrescriptionApplyInfo'
                }
                this.$post(url,{
                    Id: this.id
                }).then( m => {
                    this.datas = m
                    if(this.datas.ConsultImgs) {
                        this.datas.ConsultImgs.forEach(item => {
                            this.imgList.push(item.PicturePath)
                        })
                    }
                    
                })
            },
            toIM () {
                let o = {}
				 o.PatientInfo = {
                    PatientId: this.datas.PatientId,
                    PatientName: this.datas.PatientName,
                    PatientSex: this.datas.PatientSex
                }
                o.ApplicantHeadImgUrl = this.datas.ApplicantHeadImgUrl
                o.ApplicantIMId = this.datas.ApplicantIMId
                o.ApplicantId = this.datas.ApplicantId
                this.$bridge.callhandler('toSingleChat', JSON.stringify(o))
			}
        },
    }



